*,
body {
	font-family: 'Montserrat', sans-serif !important;
}
.fab{
	font-family: "Font Awesome 5 Brands" !important;
}
.fas,.fa{
	font-family: "Font Awesome 5 Free" !important;
}
.las{
	font-family: 'Line Awesome Free'!important;
}
.bg-brand {
	background-color: #094 !important;
}
.sidebarResponsive {
	display: flex;
}

.stepperRightHand {
	width: 100%;
	background-color: rgb(245, 248, 250);
	padding: 30px;
	padding-left: 0px;
}
.draftModal .modal-header {
	border-bottom: 1px solid #fff;
}
.draftModal .modal-footer {
	border-top: 1px solid #fff;
}

@media only screen and (max-width: 600px) {
	.sidebarResponsive {
		flex-wrap: wrap;
	}

	.sidebarResponsive .stepperRightHand {
		padding: 20px;
	}
}

.btn-stepper {
	background-color: #009444 !important;
	color: #fff!important;
	border-color: #009444!important;
}
/* .btn-stepper:hover {
	color: #009444!important;
	background-color: #fff!important;
} */
.draftModal .close{
	font-size: 24px;
}
.draftModal .modal-content{
	padding: 0 20px;
}


.btn-light-stepper {
	color: #009444 !important;
	background-color: #fff !important;
}

.btn-light-stepper:hover {
	color: #fff!important;
	background-color: #009444 !important;
}

.btn-active-stepper {
	color: #fff;
	background-color: #009444;
}

.btn-active-stepper:hover {
	color: #009444;
	background-color: #fff;
}

.stepperRegisterNav {
	display: inline-block;
	border-radius: 5px;
	box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1);
	margin-bottom: 35px;
}

.stepperRegisterNav .nav-item {
	display: inline-block;
}

.stepperRegisterNav .nav-item a.nav-link {
	font-size: 14px;
	color: #444444;
	font-weight: 500;
	text-transform: uppercase;
	padding: 12px 30px 10px;
	border-radius: 5px;
}

.stepperRegisterNav .nav-item a.nav-link.active {
	background: linear-gradient(90deg, #11b67a 0%, #009444 100%);
	color: #ffffff;
}

.newStepperRightSide {
	background: white;
	padding: 30px;
	position: relative;
	height: 100%;
}

.viewall-btn a,
.viewall-btn button
{
	font-size: 15px;
	color: #fff;
	background: linear-gradient(90deg, #11b67a 0%, #009444 100%);
	display: inline-block;

	height: auto !important;
	text-transform: uppercase;
	font-weight: 500;
	text-align: center;
	padding: 14px;
	border-radius: 5px;
	margin-top: 20px;
}

.viewall-btn a:hover {
	background: linear-gradient(90deg, #009444 0%, #11b67a 100%);
}

.desc p {
	text-overflow: ellipsis;
	overflow: hidden;
	display: -webkit-box;
	-webkit-line-clamp: 3;
	-webkit-box-orient: vertical;
}

.adminNav .nav {
	display: inline-block;
	/* border-radius: 5px; */
	/* box-shadow: 0 8px 25px rgba(0, 0, 0, 0.1); */
	/* margin-bottom: 35px; */
	width: 100%;
}

.adminNav .nav .nav-item {
	padding: 20px;
	display: inline-block;
}

.adminNav .nav .nav-item a.nav-link {
	font-size: 14px;
	font-weight: 500;

	text-transform: uppercase;
	padding-bottom: 10px;
}

.adminNav .nav .nav-item a.nav-link {
	color: #444;
}

.adminNav .nav .nav-item a.nav-link.active {
	/* background: linear-gradient(90deg, #11b67a 0%, #009444 100%); */
	border-bottom: 3px solid #11b67a;

	color: #11b67a;
}

