	background-color: #094;
	color: #fff;
	font-weight: 600;
	font-size: 0.8rem;
}

.hWydXE {
	border-bottom: 1px solid #fff !important;
}

.MuiDataGrid-cell .btn-light-stepper,
.MuiDataGrid-cell .btn-light-stepper:hover {
	color: #fff;
	background-color: #009444;
	font-weight: bold;
}

.MuiDataGrid-root {
	height: 500px;
	/* or any other valid height value */
}

.stepperRightHand .editStepperButton {
	outline: none;
	color: #fdfdfd !important;
	background: #094;
	padding: 5px 10px;
	border: none;
	border-radius: 6px;
	margin-right: 10px;
}

.stepperRightHand .deleteStepperButton {
	outline: none;
	color: #fff !important;
	background: rgba(255, 0, 0, 0.7);
	padding: 5px 10px;
	border: none;
	border-radius: 6px;
	margin-right: 10px;
}

/* Tooltip  */

.tooltipCustom {
	position: relative;

}

.tooltipCustom .tooltiptext {
	visibility: hidden;
	top: 50%;
	left: 5%;
font-size: 10px;
	background-color: black;
	color: #fff;
	text-align: center;
	border-radius: 6px;
	padding: 5px;
	/* Position the tooltipCustom */
	position: absolute;
	z-index: 1;
}

.tooltipCustom:hover .tooltiptext {
	visibility: visible;
}


